@mixin flex($direction: row, $main: center, $cross: center)
{
  display: flex;
  flex-direction: $direction;
  justify-content: $main;
  align-items: $cross;
}
@mixin grid($columns, $rows)
{
  display: grid;
  grid-template-columns: repeat($columns, 1fr);
  grid-template-rows: repeat($rows, 1fr);
}
@mixin setColor($color, $backgroundColor)
{
  color: $color;
  background-color: $backgroundColor;
}
@mixin generic-button($backgroundColor, $color)
{
  padding: 0.3rem 0.6rem;
  border-radius: 0.3rem;
  @include setColor($color, $backgroundColor);
  height: auto;
  max-height: 100%;
}

$black: #222;
$main: #4d5a97;

@function getBlack()
{
  @return $black;
}
@function getMain()
{
  @return $main;
}

@mixin generic-text-button($backgroundColor: $black, $color: whitesmoke)
{
  @include generic-button($backgroundColor, $color);
  font-size: 1.2rem;
}
@mixin generic-icon-button($height: 100%, $backgroundColor: $black, $color: whitesmoke)
{
  @include generic-button($backgroundColor, $color);
  @include flex();
  i {font-size: 1.5rem;}
}

@mixin beat-container($width, $ratio, $columns)
{
  width: $width;
  aspect-ratio: $ratio;
  @include grid($columns, 2);
  gap: 0.3rem;
  > *
  {
    @include setColor(whitesmoke, $black);
    border-radius: 5px;
    @include flex();
  }
  .selected {@include setColor($black, whitesmoke);}
}
@mixin slider($chrome)
{
  -webkit-appearance: none;
  border: 1px solid $black;
  --width: 0.9rem;
  width: var(--width);
  aspect-ratio: 1 / 2;
  border-radius: 0.2rem;
  background: #AAA;
  cursor: pointer;
  @if $chrome
  {
    margin-top: calc(var(--width) * -0.8); /* You need to specify a margin in Chrome, but in Firefox and IE it is automatic */
  }
}
@mixin track()
{
  -webkit-appearance: none;
  background: $black;
  height: 0.3rem;
}