@import 'mixins';

.beats
{
  aspect-ratio: 2 / 1;
  max-width: 72rem;
  @media (min-aspect-ratio: 2/1){height: 90vh;}
  @media (min-aspect-ratio: 1/1) and (max-aspect-ratio: 2/1){width: 90vw;}
  // 0.3rem -> 5px
  border-radius: 0.6rem;
  padding: 0.6rem 1.2rem;
  @include flex(column, center, flex-start);
  --gap: 0.6rem;
  gap: var(--gap);
  > *{width: 100%;}
  background-color: getMain();
  .top
  {
    height: 20%;
    @include flex(row, flex-start);
    gap: 1.2rem;
    position: relative;
    .beat-letter-container{@include beat-container(9%, 1 / 1, 2);}
    .beat-number-container{@include beat-container(36%, 4 / 1, 8);}
    .switches
    {
      position: absolute;
      right: 0;
      @include flex();
      gap: 1.8rem;
      > *
      {
        @include flex(column);
        @include setColor($color: whitesmoke, $backgroundColor: getBlack());
        padding: 0.6rem;
        gap: 0.3rem;
        width: 5rem;
        border-radius: 0.3rem;
        .mode
        {
          font-size: 1.2rem;
        }
        .switch
        {
          width: 80%;
          height: 1rem;
          position: relative;
          background-color: getBlack();
          border: 1px solid whitesmoke;
          .square
          {
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 50%;
            background-color: whitesmoke;
          }
          .square.playSong,
          .square.lasso
          {
            left: 50%;
          }
        }
      }
    }
  }
  .main
  {
    height: calc(70% - 2 * var(--gap));
    @include flex(row, flex-start);
    gap: 0.6rem;
    > * {height: 100%;}
    .sounds-container
    {
      width: 20%;
      @include grid(1, 8);
      .sound
      {
        @include flex();
        @include setColor($color: whitesmoke, $backgroundColor: getBlack());
        border: 1px solid #555;
      }
    }
    .notes-container
    {
      aspect-ratio: 2 / 1;
      @include grid(16, 8);
      .note
      {
        border: 1px solid #555;
        background-color: getBlack();
      }
      .note.focus{border: 2px solid whitesmoke;}
      position: relative;
      .selection
      {
        border: 2px solid whitesmoke;
        position: absolute;
        background-color: rgba($color: #000000, $alpha: 0.2);
      }
      .track-line
      {
        position: absolute;
        height: 100%;
        width: 2px;
        background-color: whitesmoke;
      }
    }
    .note-options
    {
      @include flex(column, flex-start, flex-start);
      gap: 0.6rem;
      button { @include generic-text-button(); }
    }
  }
  .bottom
  {
    height: 10%;
    @include flex(row, space-between);
    > *
    {
      height: 100%;
      @include flex();
      gap: 0.6rem;
    }
    .left button
    {
      @include generic-icon-button($backgroundColor: whitesmoke, $color: getBlack());
    }
    .left a.download-link
    {
      text-decoration: none;
      @include generic-text-button();
    }
    .right button
    {
      @include generic-text-button();
    }
  }
}