@import 'mixins';

.tutorial
{
  aspect-ratio: 1 / 1;
  width: 90vh;
  max-width: 36rem;
  // 0.3rem -> 5px
  position: absolute;
  background-color: getMain();
  border-radius: 0.6rem;
  padding: 0.6rem 1.2rem;
  @include flex(column);
  --gap: 0.6rem;
  gap: var(--gap);
  .sections-container
  {
    height: 90%;
    @include flex(column, flex-start);
    gap: 0.6rem;
    overflow-y: auto;
    .section
    {
      width: 100%;
      border-top-left-radius: 0.6rem;
      border-bottom-left-radius: 0.6rem;
      padding: 0.6rem 1.2rem;
      @include setColor(whitesmoke, getBlack());
      font-size: 1rem;
      line-height: 1.5rem;
      .title
      {
        font-size: 2rem;
        margin-bottom: 0.6rem;
      }
      img
      {
        max-width: 100%;
        margin: 0.6rem 0;
      }
    }
  }
  .sections-container::-webkit-scrollbar {width: 0.6rem;}
  .sections-container::-webkit-scrollbar-track {background: getBlack();}
  .sections-container::-webkit-scrollbar-thumb {background: whitesmoke;}
  .sections-container::-webkit-scrollbar-thumb:hover {background: whitesmoke;}
  button.close
  {
    @include generic-text-button();
    align-self: flex-end;
  }
}