@import 'mixins';

.sounds
{
  aspect-ratio: 1 / 2;
  height: 90vh;
  max-height: 36rem;
  // 0.3rem -> 5px
  position: absolute;
  background-color: getMain();
  border-radius: 0.6rem;
  padding: 0.6rem 1.2rem;
  @include flex(column);
  --gap: 0.6rem;
  gap: var(--gap);
  > * {width: 100%;}
  .top
  {
    height: 20%;
    @include flex(column);
    gap: 0.6rem;
    color: whitesmoke;
    label {font-size: 1.2rem;}
    .input-container
    {
      width: 100%;
      @include flex();
      
      > *
      {
        height: 2rem;
      }
      input
      {
        width: 75%;
        padding: 0.3rem 0.6rem;
        font-size: 1rem;
        border: 1px solid getBlack();
      }
      input:focus-visible {outline: none;}
      button.search
      {
        @include setColor($color: whitesmoke, $backgroundColor: getBlack());
        padding: 0.3rem 0.6rem;
        i{font-size: 1rem;}
      }
    }
  }
  .list
  { 
    height: calc(70% - 2 * var(--gap));
    @include flex(column, flex-start);
    gap: 0.6rem;
    overflow-y: scroll;
    overflow-x: hidden;
    .sound { @include generic-text-button(); }
    .sound.selected
    {
      @include setColor($color: getBlack(), $backgroundColor: whitesmoke)
    }
  }
  .list::-webkit-scrollbar {width: 0.6rem;}
  .list::-webkit-scrollbar-track {background: getBlack();}
  .list::-webkit-scrollbar-thumb {background: whitesmoke;}
  .list::-webkit-scrollbar-thumb:hover {background: whitesmoke;}

  .bottom
  {
    @include flex(row, space-between);
    width: 100%;
    height: 10%;
    > * { @include generic-text-button(); }
  }
}