@import 'mixins';

.song
{
  aspect-ratio: 2 / 1.2;
  width: 60vw;
  max-width: 36rem;
  // 0.3rem -> 5px
  position: absolute;
  background-color: getMain();
  border-radius: 0.6rem;
  padding: 0.6rem 1.2rem;
  @include flex(column);
  --gap: 0.6rem;
  gap: var(--gap);
  > *
  {
    width: 100%;
  }
  .song-info
  {
    height: 20%;
    @include flex(row, space-between);
    color: whitesmoke;
    font-size: 1.3rem;
    .duration
    {
      @include flex();
      gap: 0.6rem;
    }
    .name
    {
      @include flex();
      gap: 0.6rem;
      button.update-name
      {
        margin-left: 0.6rem;
        @include generic-text-button();
      }
      input
      {
        width: 6rem;
        padding: 0.3rem 0.6rem;
        font-size: 1.2rem;
      }
    }
  }
  .beats-list-container
  {
    height: calc(60% - var(--gap));
    overflow-x: scroll;
    overflow-y: hidden;
    .beats-list
    {
      height: 100%;
      @include flex();
      .beat
      {
        height: 100%;
        @include flex(column);
        > *
        {
          width: 100%;
          @include flex();
          @include setColor(whitesmoke, getBlack());
          border: 1px solid #555;
        }
        .key
        {
          height: 80%;
          font-size: 2rem;
          form
          {
            width: 80%;
            @include flex(column);
            gap: 0.6rem;
            input
            {
              width: 100%;
              padding: 0.3rem 0.6rem;
              font-size: 2rem;
            }
            .submit {@include generic-text-button();}
          }
        }
        .index
        {
          height: 20%;
          font-size: 1.2rem;
        }
      }
      .beat:hover
      {
        cursor: pointer;
      }
      .beat.selected > .key
      {
        @include setColor(getBlack(), whitesmoke);
      }
    }
  }
  .beats-list-container::-webkit-scrollbar
  {
    height: 0.6rem;
  }
  .beats-list-container::-webkit-scrollbar-track {background: getBlack();}
  .beats-list-container::-webkit-scrollbar-thumb {background: whitesmoke;}
  .beats-list-container::-webkit-scrollbar-thumb:hover {background: whitesmoke;}

  .controls
  {
    height: 20%;
    @include flex(row, space-between);
    > *
    {
      @include flex();
      height: 100%;
    }
    .left
    {
      gap: 0.6rem;
      button.left, button.right { @include generic-icon-button();}
      button.edit, button.delete { @include generic-text-button(); }
    }
    .right
    {

      button{@include generic-text-button();}
    }
  }
}